body {
  margin: 0;
  background-color: #7766c6;
  font-family: 'Comfortaa', 'Montserrat Alternates';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  min-height: 100dvh;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  overflow-x: hidden;
}

#root,
#root > div {
  height: 100%;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: node;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  line-height: normal;
}
